import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {NotificationtabsComponent} from './notificationtabs.component';
import {AboutusPage} from '../aboutus/aboutus.page';

const routes: Routes = [
  {
    path: 'notificationtabs',
    component: NotificationtabsComponent,
    children: [{
                path: '',
                redirectTo: '/notificationtabs/(aboutus:aboutus)',
                pathMatch: 'full',
              },
              {
                path: 'aboutus',
                outlet: 'aboutus',
                component: AboutusPage
               }]},
               {
                path: '',
                redirectTo: '/notificationtabs/(aboutus:aboutus)',
                pathMatch: 'full',
               }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class NotificationtabsRoutingModule { }
